<template>
  <v-app style="overflow: hidden!important;">
    <!--  overlay snackbar,   -->
    <v-overlay :value="overlay" z-index="500">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <v-snackbar v-model="showSnackbar" :timeout="2000" :color="snackbarType" top>
      {{ snackbarMessage }}
    </v-snackbar>
    <router-view/>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  data: () => ({
    overlay: false,
    showSnackbar: false,
    snackbarMessage: '',
    snackbarType: '',
  }),
  computed: {
    //
    isDark: function () {
      return this.$vuetify.theme.dark;
    },
    //
    whiteOpacity: function () {
      return this.isDark ? 'rgba(255,255,255,0.1)' : 'rgba(255,255,255,0.3)';
    },
    backgroundStyle: function () {
      return this.isDark ? 'darkBg' : 'lightBg';
    }
  },
  methods: {
    //ElementUI
    //app=this.$root.children[0]App.vue
    //this.app.message("message",type)
    message(message, type) {
      this.showSnackbar = true;
      this.snackbarMessage = message;
      this.snackbarType = type;
    },
    //token
    updateToken() {
      //  expires 
      // 
      if (this.$store.state.token && this.$store.state.expiredAt) {
        let expiredAt = new Date(this.$store.state.expiredAt);
        let now = new Date();
        if (now >= expiredAt) {
          this.$store.commit("logout");
        }
      }
      setTimeout(() => {
        this.updateToken();
      }, 60000)
    },
    // redirectUserHome() {
    //   if (this.$store.state.token && this.$store.state.expiredAt) {
    //     let expiredAt = new Date(this.$store.state.expiredAt);
    //     let now = new Date();
    //     if (now < expiredAt) {
    //       this.$router.replace('/userHome');
    //     }
    //   }
    // }
  },
  mounted() {
    //this.redirectUserHome();
    this.updateToken();
  }
};
</script>

